import React, { useState, useEffect } from 'react'
import List from './List'
import Alert from './Alert'

const getLocalStorage = () => {
  const list = localStorage.getItem('items');
  if (list) {
    return JSON.parse(list);
  }
  else return [];
}



function App() {
  let [groceryItem, setGroceryItem] = useState(''); // single grocery item
  let [items, setItems] = useState(getLocalStorage()); // list of all grocery items
  let [isAlert, setIsAlert] = useState(false);
  let [alert, setAlert] = useState('');
  let [content, setContent] = useState('');
  let [edit, setEdit] = useState(false);
  let [editItem, setEditItem] = useState('');

  const remove_alert = () => {
    setTimeout(() => {
      setAlert('');
      setContent('');
      setIsAlert(false);
    }, 2500);
  }

  useEffect(() => {
    localStorage.setItem('items' , JSON.stringify(items));
  },[items])

  return <section className="section-center">
    {
      isAlert && <Alert alert={alert} content={content}/>
    }
    <form className="grocery-form" onSubmit={(e) => {
      e.preventDefault();
      if (!edit) {
        if (!groceryItem) {
        setIsAlert(true)
        setAlert('alert-danger');
        setContent('Please enter value!');
        }
        else {
          setIsAlert(true)
          setAlert('alert-success');
          setContent('Item added successfully')
          setItems([...items, groceryItem]);
          setGroceryItem('');
        }
        
        remove_alert();
      }
      else {
        if (!groceryItem) {
          setIsAlert(true)
          setAlert('alert-danger');
          setContent('Please enter value!');
        }
        else {
          setItems(items.map((element) => {
            if (element === editItem) {
              return groceryItem;
            }
            else {return element}
          }));
          setIsAlert(true)
          setAlert('alert-success');
          setContent('Value changed');
          remove_alert();
          
          setGroceryItem('');
          setEdit(false);
        }
         
      }
    }}>
      <h3>To-Do List</h3>
      <div className="form-control">
        <input type="text" placeholder='Your Tasks Here ...' className='grocery' id='input' value={groceryItem} onChange={(e) => {
          setGroceryItem(e.target.value);
        }} />
        <button type='submit' className='submit-btn'>{edit ? 'Edit Task' : 'Add Task' }</button>
      </div>
     </form>
    <List groceryItem={groceryItem} setGroceryItem={setGroceryItem} Items={items} setItems={setItems} setAlert={setAlert} setContent={setContent} setEdit={setEdit} setEditItem={setEditItem} remove_alert={remove_alert} setisalert={setIsAlert}/>
  </section>
}

export default App
