import React from 'react'
import { FaEdit, FaTrash } from 'react-icons/fa'
const List = ({groceryItem , Items , setItems , setGroceryItem , setAlert , setContent , setEdit , setEditItem , remove_alert , setisalert}) => {
  return <div className="grocery-container">
    {
      Items.map((item,index) => {
        return <div className="grocery-item" key={index}>
      <p className='title'>{item}</p>
      <div>
            <FaEdit className='edit-btn' onClick={() => {
              setEdit(true);
              setEditItem(item);
              document.getElementById('input').focus();
              setGroceryItem(item);
        }} />
            <FaTrash className='delete-btn' onClick={() => {
              setItems(Items.filter((elem) => elem !== item));
              setisalert(true);
              setContent('Item removed!');
              setAlert('alert-danger');
              remove_alert();
        }} />
      </div>
    </div>
      })
    }
    <button className='clear-btn' onClick={() => {
      setisalert(true);
      setAlert('alert-danger');
      setContent('Tasks Cleared!')
      setItems([]);
      remove_alert();
    }}>Clear Tasks</button>
  </div>
}

export default List
